import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';



export default function Login({props}:any) {
    const data = props.data;
    const user = props.user;         const setUser = props.setUser;
    const password = props.password; const setPassword = props.setPassword;
    const loggedIn = props.loggedIn; const setLoggedIn = props.setLoggedIn;

    const navigate = useNavigate();

    useEffect(() => {
        // create keys on startup if not exist
        if (!localStorage.getItem('leftNotes')) {
            localStorage.setItem('leftNotes', JSON.stringify(''));
        }

        if (!localStorage.getItem('rightNotes')) {
            localStorage.setItem('rightNotes', JSON.stringify(''));
        }

        if (!localStorage.getItem('leftHeader')) {
            localStorage.setItem('leftHeader', JSON.stringify('Vnster'));
        }

        if (!localStorage.getItem('rightHeader')) {
            localStorage.setItem('rightHeader', JSON.stringify('Hger'));
        }


        for (var key in localStorage){
            console.log(key)
         }


        async function fetchData() {
            if (loggedIn) {
                navigate("/logs")
            }
        }
        // fetch data
        fetchData();

      }, [loggedIn]); // when loggedIn changes to 'true', useEffect runs again and renavigates



    const submit = (e: React.SyntheticEvent<HTMLFormElement>) => {
        e.preventDefault();
        let passwordInput = document.getElementsByClassName("password")[0] as HTMLFormElement;
        let errorText = document.getElementsByClassName("errorText")[0] as HTMLElement;
        passwordInput.style.outline = "1px solid black";

        data.users.every((userObject:any) => {
            if (
                userObject.username === user && // user is input
                userObject.password === password // password is input
            ) {
                console.log("LOGGADE IN")
                return setLoggedIn(true)
            } else if (
                userObject.username === user &&
                !(userObject.password === password)
            ) {
                passwordInput.style.outline = "3px solid red";
                errorText.style.visibility = "visible";
                errorText.style.color = "red";
            }
        });

    }

    
  return (
    <div className="Login">

      {/* Detta r sidan 'Login'
      S fort anvndaren loggar in s stts loggedIn till true och menyn syns. */}

        <form className="login" onSubmit={submit}>
            <label>Anvndare</label>
            <input
                className="username"
                type="text"
                autoComplete="on"
                onChange={(e) => setUser(e.target.value.toLowerCase())}
                value={user}
                required
            />

            <label>Lsenord</label>
            <input
                className="password"
                type="password"
                autoComplete="on"
                onChange={(e) => setPassword(e.target.value)}
                value={password}
                required
            />
            <p className="errorText" style={{visibility: "hidden"}}>Lsenordet r fel</p>


            <input type="submit" value="Logga in" />
        </form>
    </div>
    );
}